<template>
  <div class="feedback">
    <!-- 头部 -->
    <mu-appbar title="意见反馈" class="muAppbar" >
      <mu-icon-button icon="navigate_before" slot="left" to="/helping" />
      <mu-icon-menu icon="more_vert" slot="right">
        <mu-menu-item title="刷新" @click="reForm()" />
      </mu-icon-menu>
    </mu-appbar>
    
    
    <!-- 意见反馈 -->
    <div class="radio-list">
      <mu-sub-header>意见反馈(您遇到的问题类型)</mu-sub-header>
      <mu-radio label="产品建议" name="group" nativeValue="产品建议" v-model="value" class="demo-radio"/>
      <mu-radio label="音乐人" name="group" nativeValue="音乐人" v-model="value" class="demo-radio"/>
      <mu-radio label="功能" name="group" nativeValue="功能" v-model="value" class="demo-radio"/>
      <mu-radio label="vip" name="group" nativeValue="vip" v-model="value" class="demo-radio"/>
      <mu-radio label="账号相关" name="group" nativeValue="账号相关" v-model="value" class="demo-radio"/>
      <mu-radio label="其它" name="group" nativeValue="其它" v-model="value" class="demo-radio"/>
    </div>

    <!-- 建议 -->
    <mu-text-field hintText="请写下建议（默认 3行最大6行）" multiLine :rows="3" :rowsMax="6" fullWidth v-model="suggest"/>

    <!-- 照片 -->

    <el-upload action="https://jsonplaceholder.typicode.com/posts/" list-type="picture-card":on-preview="handlePictureCardPreview" :on-remove="handleRemove" class="elUpload">
      <i class="el-icon-plus"></i>
    </el-upload>
    <el-dialog v-model="dialogVisible" size="small" >
      <img width="100%" :src="dialogImageUrl" alt="" fullWidth>
    </el-dialog>

    <!-- 手机或邮箱 -->
    <mu-text-field hintText="填写您的手机或邮箱" fullWidth  class="PhoneorEmail" v-model="Phone"/>

    <!-- 提交 -->
    <div class="demo-raised-button1">
      <mu-raised-button label="提交"  primary @click="submitForm()"/>
    </div>

    <!-- 在线客服 -->
    <div class="demo-raised-button2">
      <mu-raised-button label="在线客服" solt="left" icon="headset" class="online" />
      <div class="timing">工作时间(工作日10: 00-17: 00)</div>
    </div>

  </div>
</template>

<script>
export default {
  data () {
    return {
        value: '产品建议',
        suggest:"",
        Phone:"",
        list:{
        value: '产品建议',
        suggest:"",
        Phone:""
        },
        dialogImageUrl: '',
        dialogVisible: false

    }
  },
  methods: {
    // 上传图片
      handleRemove(file, fileList) {
        console.log(file, fileList);
      },
      handlePictureCardPreview(file) {
        this.dialogImageUrl = file.url;
        this.dialogVisible = true;
      },
      // 提交
      submitForm(){
        console.log(this.value);
        console.log(this.suggest);
        console.log(this.Phone);
      },
      // 刷新重置
      reForm(){
        this.value=this.list.value;
        this.suggest=this.list.suggest;
        this.Phone=this.list.Phone;
      }
    }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.muAppbar{
position: fixed;
}
.demo-radio{
  width: 32.5%;
  height: 40px;
}
.radio-list{
  padding-top: 50px;
  border-bottom: 6px solid #E0E0E0;
}
.el-upload{
  width: 50px !important;
  height: 50px !important;
}
.el-upload--picture-card {
  width: 50px !important;
  height: 50px !important;
}
.PhoneorEmail{
  border-top: 6px solid #E0E0E0;
  border-bottom: 6px solid #E0E0E0;
}
.demo-raised-button1,.demo-raised-button2{
  text-align: center;
}
.demo-raised-button2{
margin-top: 10px;
}
.online{
  border:1px solid black; 
}
.timing{
  color: #9F9F9F;
  font-size: 12px;
}
</style>


