<template>
  <div class="footerGuide">
    <mu-appbar class="muAppbar">
      <mu-icon-button icon="menu" @click="toggle()"  slot="left" />
       <!-- <mu-avatar slot="right"  backgroundColor="purple500">头像</mu-avatar> -->
       
      <!-- 汉堡内容 -->
      <mu-drawer left :open="open" @close="toggle()">
        <mu-appbar title="Music" v-if="docked" @click.native="open = false">
          <mu-icon slot="right" value="clear"/>
        </mu-appbar>
        <mu-list>
          <mu-list-item title="个人中心" to="/Personal_Center">
            <mu-icon slot="left" value="person"/>
            <mu-icon slot="right" value="keyboard_arrow_right"/>
          </mu-list-item>
          <mu-list-item title="消息中心" to="/Message_Center">
            <mu-icon slot="left" value="message"/>
            <mu-icon slot="right" value="keyboard_arrow_right"/>
          </mu-list-item>
          <mu-list-item title="定时关闭" to="/Timing">
            <mu-icon slot="left" value="alarm"/>
            <mu-icon slot="right" value="keyboard_arrow_right"/>
          </mu-list-item>
          <mu-list-item title="个性换肤" to="/personality">
            <mu-icon slot="left" value="color_lens" />
            <mu-icon slot="right" value="keyboard_arrow_right"/>
          </mu-list-item>
          
          <mu-divider />

          <mu-list-item title="设置" to="/setting">
            <mu-icon slot="left" value="settings"/>
            <mu-icon slot="right" value="keyboard_arrow_right"/>
          </mu-list-item>
          <mu-list-item title="帮助与反馈" to="/helping">
            <mu-icon slot="left" value="help_outline"/>
            <mu-icon slot="right" value="keyboard_arrow_right"/>
          </mu-list-item>

          <mu-divider />

          <mu-list-item title="退出" to="/personality">
            <mu-icon slot="left" value="power_settings_new"/>
          </mu-list-item>
        </mu-list>
    </mu-drawer>
    <!-- 搜索框 -->
      <!-- <mu-text-field icon="search" class="appbar-search-field"  slot="right" hintText="请输入搜索内容"/> -->
    
    </mu-appbar>
    <!-- 脚部 -->
    <mu-paper class="bottomFixed">
	    <mu-bottom-nav :value="bottomNav" @change="handleChange">
		    <mu-bottom-nav-item value="home" title="首页" to="/footerGuide/index" icon="home"/>
		    <mu-bottom-nav-item value="queue_music" title="我的音乐" to="/footerGuide/myMusic" icon="queue_music"/>
		    <mu-bottom-nav-item value="explore" title="听友圈" to="/footerGuide/friends" icon="explore"/>
	    </mu-bottom-nav>
	 </mu-paper>

    <router-view></router-view>
  </div>
</template>

<script>
export default {
    data () {
      return {
        bottomNav: 'home',
        open: false,
        docked: true
        }
    },
    methods: {
      handleChange (val) {
        this.bottomNav = val
      },
      toggle (flag) {
        this.open = !this.open
        this.docked = !flag
      }
    }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.bottomFixed{
	position: fixed;
  width: 100%;
  bottom: 0;
  z-index:1000;
}
.mu-appbar{
  border-bottom: 1px solid white; 
}

</style>


