<template>
  <div class="index">
    <!-- tab切换 -->
    <mu-tabs :value="activeTab" @change="handleTabChange" class="mu-tabs">
    <mu-tab value="tab1" title="热门推荐" class="mu-tab"/>
    <mu-tab value="tab2" title="歌单" class="mu-tab"/>
    <mu-tab value="tab3" title="排行榜" class="mu-tab"/>
    <mu-tab value="tab4" title="歌手" class="mu-tab"/>
    </mu-tabs>
    <!--  -->
    <div v-if="activeTab === 'tab1'">

      <mt-swipe :auto="4000" class="shuffling">
        <mt-swipe-item>
          <img src="https://y.gtimg.cn/music/common/upload/t_focus_info_iphone/135040.jpg" class="shuffling_image">
          </mt-swipe-item>
        <mt-swipe-item>
          <img src="https://y.gtimg.cn/music/common/upload/t_focus_info_iphone/134950.jpg" class="shuffling_image">
        </mt-swipe-item>
        <mt-swipe-item>
          <img src="https://y.gtimg.cn/music/common/upload/t_focus_info_iphone/133922.jpg" class="shuffling_image">
        </mt-swipe-item>
      </mt-swipe>
      <!-- 推荐歌单 -->
      <mu-grid-list class="gridlist-demo">
        <mu-sub-header>推荐歌单</mu-sub-header>
        <mu-grid-tile v-for="tile,index in list" :key="index">
          <img :src="tile.image"/>
          <span slot="title">{{tile.title}}</span>
          <span slot="subTitle">by <b>{{tile.author}}</b></span>
          <mu-icon-button icon="star_border" slot="action"/>
        </mu-grid-tile>
      </mu-grid-list>

    </div>

    <div v-if="activeTab === 'tab2'">
      <h2>Tab Two</h2>
      <p>
        这是第二个 tab
      </p>
    </div>

    <div v-if="activeTab === 'tab3'">
      aa
    </div>

    <div v-if="activeTab === 'tab4'">
      bb
    </div>
  
  </div> 
</template>

<script>
import ImgUrl from '../assets/images/1.jpg'

export default {
  data () {
    return {
      activeTab: 'tab1',
      list: [{
        image: ImgUrl,
        title: 'Breakfast',
        author: 'Myron'
      }, {
        image: ImgUrl,
        title: 'Burger',
        author: 'Linyu'
      },
      {
        image: ImgUrl,
        title: 'Burger',
        author: 'Linyu'
      },
      {
        image: ImgUrl,
        title: 'Burger',
        author: 'Linyu'
      },
      {
        image: ImgUrl,
        title: 'Burger',
        author: 'Linyu'
      }]

    }
  },
   methods: {
    // tab切换
    handleTabChange (val) {
      this.activeTab = val
    }  
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

.shuffling{
  width: 100%;
  height: 8.125rem;
  background-color: red;
}
.shuffling_image{
  width: 100%;
  height: 8.125rem;
}
.mu-tabs{
  background-color: white;
  height: 1.5625rem;
}
.mu-tab{
  color: black;
}

</style>


