import Vue from 'vue'
import Router from 'vue-router'
import footerGuide from '@/components/footer/footerGuide'
import head from '@/components/header/head'
import personality from '@/components/common/index/personality'
import helping from '@/components/common/index/helping'
import Message_Center from '@/components/common/index/Message_center'
import Personal_Center from '@/components/common/index/Personal_Center'
import Timing from '@/components/common/index/Timing'
import setting from '@/components/common/index/setting'
import index from '@/page/index'
import myMusic from '@/page/myMusic'
import friends from '@/page/friends'
import feedback from '@/page/feedback'

Vue.use(Router)

export default new Router({
    linkActiveClass: "focus",//高亮
    routes: [
        {path: '/',redirect: '/footerGuide/index'},
        {path: '/footerGuide',component: footerGuide,
        	children:[
        		{path: 'index',component: index},
        		{path: 'myMusic',component: myMusic},
        		{path: 'friends',component: friends}
        	]
        },
        {path:'/head',component:head},
        {path:'/personality',component:personality},
        {path:'/helping',component:helping},
        {path:'/Message_Center',component:Message_Center},
        {path:'/Personal_Center',component:Personal_Center},
        {path:'/Timing',component:Timing},
        {path:'/setting',component:setting},
        {path:'/feedback',component:feedback}
        
    ]
})



