<template>
  <div class="personality">

    <mu-appbar title="个性换肤" class="head_person">
      <mu-icon-button icon="navigate_before" slot="left"  to="/footerGuide/index"/>
    </mu-appbar>

    <mu-tabs :value="theme" @change="changeTheme">
          <mu-tab title="紫番薯" value="DEFAULT" />
        <mu-tab title="蓝精灵" value="light" />
        <mu-tab title="蓝黑熊" value="dark" />
        <mu-tab title="黑白配" value="carbon" />
        <mu-tab title="抹茶绿" value="teal" />
    </mu-tabs>

    <div v-if="theme === 'DEFAULT'">
      <mu-paper class="demo-paper-DEFAULT" :zDepth="3" />
      </div>
    <div v-if="theme === 'light'">
      <mu-paper class="demo-paper-light" :zDepth="3" />
      </div>
    <div v-if="theme === 'dark'">
      <mu-paper class="demo-paper-dark" :zDepth="3" />
    </div>
    <div v-if="theme === 'carbon'">
      <mu-paper class="demo-paper-carbon" :zDepth="3" />
    </div>
    <div v-if="theme === 'teal'">
      <mu-paper class="demo-paper-teal" :zDepth="3" />
    </div>

  </div>
</template>

<script>
import DEFAULT from '!raw-loader!muse-ui/dist/theme-default.css'
import light from '!raw-loader!muse-ui/dist/theme-light.css'
import dark from '!raw-loader!muse-ui/dist/theme-dark.css'
import carbon from '!raw-loader!muse-ui/dist/theme-carbon.css'
import teal from '!raw-loader!muse-ui/dist/theme-teal.css'

 export default {
    data() {
      return {
        theme: 'light',
        themes: {
          DEFAULT,
          light,
          dark,
          carbon,
          teal
        }
      }
    },
    methods: {
      changeTheme(theme) {
        this.theme = theme
        const styleEl = this.getThemeStyle()
        styleEl.innerHTML = this.themes[theme] || ''
      },
      getThemeStyle() {
        const themeId = 'muse-theme'
        let styleEl = document.getElementById(themeId)
        if (styleEl) return styleEl
        styleEl = document.createElement('style')
        styleEl.id = themeId
        document.body.appendChild(styleEl)
        return styleEl
      }
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.head_person{
  border-bottom: 1px solid white;
}
.demo-paper-DEFAULT,.demo-paper-light,.demo-paper-dark,.demo-paper-carbon,.demo-paper-teal{
  display: inline-block;
  height: 6.25rem;
  width: 6.25rem;
  margin:1.25rem;
  text-align: center;
}
.demo-paper-DEFAULT {
  background-color:#7e57c2; 
}
.demo-paper-light {
  background-color:#2196f3; 
}
.demo-paper-dark {
  background-color:rgba(0,0,0,.87); 
}
.demo-paper-carbon {
  background-color:#474a4f; 
}
.demo-paper-teal {
  background-color:#009688; 
}
</style>



